/*
 *  X
 * degress:  
 * r:  
 * adjust:    
 * x:   X 
 */
;

import getRad from './degrees-to-radian';

function main(degrees: number, r: number, adjust: number, x: number): number {
    var x = x || r,
        adj = adjust || 1;
    return x + r * adj * Math.cos(getRad(degrees));
}

/**
 * 
 */
export {
    main as default,
};


