/*
 *  Y
 * degress:  
 * r:  
 * adjust:   
 * y:   Y 
 */
;

import getRad from './degrees-to-radian';

function main(degrees: number, r:number, adjust: number, y: number): number {
    var y = y || r,
        adj = adjust || 1;
    return y + r * adj * Math.sin(getRad(degrees));
}

/**
 * 
 */
export {
    main as default,
};


